(function() {
  angular.module('misstomissey', ['ngAnimate', 'ngTouch', 'ngRoute', 'mm.foundation']);

}).call(this);



(function() {
  angular.module('misstomissey').directive('ourstory', [
    '$timeout', function($timeout) {
      var OurStoryController, directive;
      OurStoryController = function() {
        var vm;
        vm = this;
        vm.slides = [
          {
            bg: "/assets/images/siue.jpg",
            blurb: "In 2007, Marc and Bridget were freshman at SIUE...living in the same wing...of the same dorm. They lived in a really fun wing, so everyone loved hanging out together and making friends. While the rest of the group ate most of their dinners together, Bridget would eat her meals with her older sister, Ella, who was a sophomore. Marc had seen Bridget around and thought she was really cute, so to get her attention Marc would yell Bridget’s name across the cafeteria to get her to sit by him. It was really cute and Bridget would be lying if she said she didn’t like the attention. Things just kinda clicked at a party they both were at in Edwardsville. They’ll save you from the rest of those boring details…",
            year: "2007"
          }, {
            bg: "/assets/images/sf.jpg",
            blurb: "Fast forward to 2011, when Marc and Bridget were seniors in college. They wanted to go somewhere for spring break, and while going to Panama City would have been a lot of fun they decided to go to San Francisco instead. They both knew they didn’t want to stay in St. Louis after graduating, so checking out the tech scene in SF was a must for Marc. And Bridget was enjoying being along for the ride. Long story short, but they fell in love with the City by the Bay (never San Fran and never ever Frisco!) and moved out there after Marc graduated that summer. They learned a whole lot of life lessons along the way and a whole lot about each other. They spent 4.5 fantastic years in the Bay Area and they left a piece of their hearts there.",
            year: "2011"
          }, {
            bg: "/assets/images/denver.jpg",
            blurb: "In 2015, 8 years into their relationship, Marc and Bridget were starting to feel antsy. They were sick of apartment life and they were sick of those Bay Area rent prices. They started talking about moving and Denver was at the top of the list. Once they got serious about that idea, things started moving pretty quickly. They had also been talking, mostly on Bridget’s part, about getting engaged. After several years of taking hints, Marc planned a day in Napa for he and Bridget. Bridget didn’t think anything of it because 1. why would she ever say no to a trip to Napa? and 2. since they were soon moving, who know’s when they will get to come back. Marc completely surprised her by popping the question at their favorite winery, Cuvaison. It was perfect and unforgettable. About a month later, they packed up and moved to Denver where they are going to live happily ever after.",
            year: "2015"
          }
        ];
        vm.prevSlideElm = document.getElementById("prevSlide");
        vm.nextSlideElm = document.getElementById("nextSlide");
        vm.activeSlideElm = document.getElementById("activeSlide");
        vm.activeSlideIndex = 0;
        vm.updateSlides = function() {
          vm.activeSlide = vm.slides[vm.activeSlideIndex];
          vm.previousSlide = vm.slides[vm.getPrev()];
          return vm.nextSlide = vm.slides[vm.getNext()];
        };
        vm.getPrev = function() {
          if (vm.activeSlideIndex > 0) {
            return vm.activeSlideIndex - 1;
          } else {
            return vm.slides.length - 1;
          }
        };
        vm.getNext = function() {
          if (vm.activeSlideIndex < vm.slides.length - 1) {
            return vm.activeSlideIndex + 1;
          } else {
            return 0;
          }
        };
        vm.goNext = function() {
          if (vm.sliding) {
            return;
          }
          vm.sliding = true;
          vm.activeSlideIndex = vm.getNext();
          vm.activeSlideElm.classList.add("slidingLeft");
          vm.nextSlideElm.classList.add("slidingLeft");
          return $timeout(function() {
            vm.activeSlideElm.classList.remove("slidingLeft");
            vm.nextSlideElm.classList.remove("slidingLeft");
            vm.updateSlides();
            return vm.sliding = false;
          }, 1000);
        };
        vm.goPrev = function() {
          if (vm.sliding) {
            return;
          }
          vm.sliding = true;
          vm.activeSlideIndex = vm.getPrev();
          vm.activeSlideElm.classList.add("slidingRight");
          vm.prevSlideElm.classList.add("slidingRight");
          return $timeout(function() {
            vm.activeSlideElm.classList.remove("slidingRight");
            vm.prevSlideElm.classList.remove("slidingRight");
            vm.updateSlides();
            return vm.sliding = false;
          }, 1000);
        };
        vm.updateSlides();
        $timeout(function() {
          return window.sparkle();
        });
      };
      return directive = {
        restrict: 'E',
        templateUrl: 'app/components/ourstory/ourstory.html',
        scope: {},
        controller: OurStoryController,
        controllerAs: 'vm',
        bindToController: true
      };
    }
  ]);

}).call(this);



(function() {
  angular.module('misstomissey').directive('navbar', [
    '$location', '$anchorScroll', function($location, $anchorScroll) {
      var NavbarController, directive;
      NavbarController = function() {
        var vm;
        vm = this;
        vm.tab = 0;
        vm.goTo = function(newHash) {
          if ($location.hash() !== newHash) {
            return $location.hash(newHash);
          } else {
            return $anchorScroll();
          }
        };
      };
      return directive = {
        restrict: 'E',
        templateUrl: 'app/components/navbar/navbar.html',
        scope: {},
        controller: NavbarController,
        controllerAs: 'vm',
        bindToController: true
      };
    }
  ]);

}).call(this);



(function() {
  angular.module('misstomissey').directive('bridalparty', [
    '$timeout', function($timeout) {
      var PartyController, directive;
      PartyController = function() {
        var party;
        party = this;
        party.slides = [
          {
            name: "Ella Kurczewski",
            title: "Matron of Honor",
            img: "/assets/images/ella.jpg",
            blurb: "blah blah slide 1"
          }, {
            name: "Josh Holtgreive",
            title: "Best Man",
            img: "/assets/images/josh.jpg",
            blurb: "blah blah slide 1"
          }, {
            name: "Rachel Wayne",
            title: "Maid of Honor",
            img: "/assets/images/rachel.jpg",
            blurb: "blah blah slide 1"
          }, {
            name: "Brian Knecht",
            title: "Groomsman",
            img: "/assets/images/brian.png",
            blurb: "blah blah slide 1"
          }, {
            name: "Stephanie Missey",
            title: "Bridesmaid",
            img: "/assets/images/steph.jpg",
            blurb: "blah blah slide 1"
          }, {
            name: "Brandon Evans",
            title: "Groomsman",
            img: "/assets/images/brandon.jpg",
            blurb: "blah blah slide 1"
          }
        ];
        party.prevSlideElm = document.getElementById("party-prevSlide");
        party.nextSlideElm = document.getElementById("party-nextSlide");
        party.activeSlideElm = document.getElementById("party-activeSlide");
        party.activeSlideIndex = 0;
        party.changeRate = window.innerWidth > 792 ? 2 : 1;
        party.updateSlides = function() {
          party.activeSlide = party.slides[party.activeSlideIndex];
          party.activeSlideRight = party.slides[party.activeSlideIndex + 1];
          party.previousSlide = party.slides[party.getPrev()];
          party.previousSlideRight = party.slides[party.getPrev() + 1];
          party.nextSlide = party.slides[party.getNext()];
          return party.nextSlideRight = party.slides[party.getNext() + 1];
        };
        party.getPrev = function() {
          if (party.activeSlideIndex > 0) {
            return party.activeSlideIndex - party.changeRate;
          } else {
            return party.slides.length - party.changeRate;
          }
        };
        party.getNext = function() {
          if (party.activeSlideIndex < party.slides.length - party.changeRate) {
            return party.activeSlideIndex + party.changeRate;
          } else {
            return 0;
          }
        };
        party.goNext = function() {
          if (party.sliding) {
            return;
          }
          party.sliding = true;
          party.activeSlideIndex = party.getNext();
          party.activeSlideElm.classList.add("slidingLeft");
          party.nextSlideElm.classList.add("slidingLeft");
          return $timeout(function() {
            party.activeSlideElm.classList.remove("slidingLeft");
            party.nextSlideElm.classList.remove("slidingLeft");
            party.updateSlides();
            return party.sliding = false;
          }, 1000);
        };
        party.goPrev = function() {
          if (party.sliding) {
            return;
          }
          party.sliding = true;
          party.activeSlideIndex = party.getPrev();
          party.activeSlideElm.classList.add("slidingRight");
          party.prevSlideElm.classList.add("slidingRight");
          return $timeout(function() {
            party.activeSlideElm.classList.remove("slidingRight");
            party.prevSlideElm.classList.remove("slidingRight");
            party.updateSlides();
            return party.sliding = false;
          }, 1000);
        };
        party.updateSlides();
      };
      return directive = {
        restrict: 'E',
        templateUrl: 'app/components/bridalparty/bridalparty.html',
        scope: {},
        controller: PartyController,
        controllerAs: 'party',
        bindToController: true
      };
    }
  ]);

}).call(this);



